@import "main";

// g
* {
    box-sizing: border-box;
}

// scss color var
$color-black: #000000; //black
$color-white: #ffffff; //white

// scss space var
$view-width: 80vw;

// type
h1 {
    font-family: "freight-big-pro", sans-serif;
    font-weight: 400;
    color: black;
    font-size: 7.25rem;
    line-height: 0.8em;
    text-transform: uppercase;
}

// h2 {
//     font-family:"neue-haas-grotesk-display";
//     font-weight: 600;
//     color: $color-black;
//     font-size: 3.5rem;
// }

h2 {
    font-family: "aw-conqueror-didot";
    font-style: normal;
    font-weight: 400;
    color: $color-black;
    font-size: 3.5rem;
    line-height: 89%;
    margin: 0;
}

h3 {
    font-family: "freight-big-pro", serif;
    font-weight: 400;
    color: $color-black;
    font-size: 2.25rem;
}

h4 {
    font-family: "freight-big-pro", serif;
    font-weight: 400;
    color: $color-black;
    font-size: 1.25rem;
    font-style: italic;
}

h5 {
    font-family: "neue-haas-grotesk-display", sans-serif;
    font-weight: 600;
    color: $color-black;
    font-size: 0.75rem;
    text-transform: uppercase;
}

@mixin p-sans {
    font-family:"aktiv-grotesk";
    font-weight: 400;
    color: $color-black;
    font-size: 1rem;
    line-height: 1.5rem;
    widows: 0;
}

@mixin p-serif {
    font-family: "freight-big-pro", serif;
    font-weight: 400;
    color: $color-black;
    font-size: 1.125rem;
}

p {
    @include p-sans();
}

a {
    font-family:"aktiv-grotesk-extended";
    font-weight: 700;
    color: $color-black;
    font-size: 1rem;

    // &:hover {
    //     text-decoration: none;
    //     background-color: yellow;
    //     transition: 0.5s;
    //     content: url()
    // }

}

blockquote {
    font-family:"aw-conqueror-didot";
    font-weight: 400;
    color: $color-black;
    font-size: 1.75rem;
}

.blackletter {
    font-family: "UnifrakturMaguntia", cursive;
    font-size: 1.2rem; 
}

// page g
body {
    background-color: white;
  }

// hor scroll
// https://redstapler.co/pure-css-horizontal-scrolling-website-tutorial/#:~:text=First%20we%20will%20create%20a,is%20in%20the%20right%20position.
@mixin block_width {
    width: 60%;
}

.e_block {
    @include block_width;
    min-width: 800px;
    max-width: 1000px;
    height: min-content;
    border-left: 1px solid $color-black;
    img {
        width: 100%;
        // height: auto;
    }
  }

.wrapper {
    display: flex;
    flex-direction: row;
    width: $view-width*4;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
}

.outer-wrapper {
    width: 100vh;
    height: 100vw;
    transform: rotate(-90deg) translateX(-100vh);
    transform-origin: top left;
    overflow-y: scroll;
    overflow-x: hidden;
    position: absolute;
}

::-webkit-scrollbar {
    display:none;
}

// general post css
.e_metadata {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    margin-top: 20px;

    .location {
        margin: 0;
        padding-left: 10px;
        padding-right: 10px;
    }

    .time {
        margin: 0;
    }
}

.img {
    display: block;
}

// post size classes
.X-Small {
    background-color: $color-white;
    display: flex;
    min-width: 450px;
    max-width: 450px;
    .e_subblock {
        width: 70%;
        margin: 20px;
    }
    .text_div {
        align-self: flex-start;
    }
    p {
        @include p-serif();
    }
}

.Small {
    background-color: $color-white;
    display: flex;
    flex-direction: row-reverse;
    .e_subblock {
        width: 50%;
        margin: 20px;
    }
    .img_div {
        align-self: flex-start;
        border-left: 1px solid $color-black;
    }
    .text_div {
        align-self: flex-start;
    }
}

.Medium {
    background-color: red;
}

.Large {
    background-color: blue;
}

.X-Large {
    background-color: orange;
}

//post custom classes
.Reflect {
    text-align: right;
    p {
        @include p-serif();
    }
    .e_metadata {
        flex-direction: row-reverse;
    }
}

.BigSpace {
    padding: 30%;
}